// Override to support the change of the visibility and editability over forms buttons
bizagi.command.controllers.action.extend("bizagi.command.controllers.action", {}, {

  /**
                                                                                     * Sets a new color for the background of the button
                                                                                     * Applies also to grid cells!
                                                                                     * @param {} xpath 
                                                                                     * @param {} color 
                                                                                     * @returns {} 
                                                                                     */
  changeButtonBackground: function changeButtonBackground(xpath, color) {
    if (this.form && !this.form.isFloatingButtonsEnabled())
    this._super(xpath, color);
  },

  /**
      * Sets a new color for the foreground of the render
      * @param {} xpath 
      * @param {} argument 
      * @returns {} 
      */
  changeButtonForeground: function changeButtonForeground(xpath, color) {
    if (this.form && !this.form.isFloatingButtonsEnabled())
    this._super(xpath, color);
  },

  /**
      * Changes the visibility of the button
      * @param {} xpath 
      * @param {} argument 
      * @returns {} 
      */
  changeButtonVisibility: function changeButtonVisibility(xpath, status) {
    var self = this;

    if (self.form && !self.form.isFloatingButtonsEnabled()) {
      self._super(xpath, status);
    } else {
      self.changeFloatingButtonVisibility(xpath, status);
    }
  },

  /**     
      * Changes the editability of the button
      * @param {} xpath 
      * @param {} argument 
      * @returns {} 
      */
  changeButtonEditability: function changeButtonEditability(xpath, status) {
    var self = this;

    if (self.form && !self.form.isFloatingButtonsEnabled()) {
      self._super(xpath, status);
    } else {
      self.changeFloatingButtonVisibility(xpath, status);
    }
  },

  /**
      * Helper to override the visibility and editability
      * @param {} xpath 
      * @param {} status 
      * @returns {} 
      */
  changeFloatingButtonVisibility: function changeFloatingButtonVisibility(xpath, visible) {
    var self = this;
    var form = self.form;
    var guid = xpath.substring(3);
    var navigation = form.getNavigation();
    var buttons = form.buttons && form.buttons.length ? form.buttons : form.properties.buttons || [];
    var isVisible = bizagi.util.parseBoolean(visible) !== false;
    var index = buttons.findIndex(function (item) {return item.id === guid;});

    if (index !== -1)
    buttons[index].visible = isVisible;

    form.buttons = buttons || [];
    navigation.setNavigationButtons(form);
  } });