  /*
      *   Changes the visibility for a cell
      */
  changeCellVisibility: function changeCellVisibility(key, xpath, argument) {
    var self = this;
    var control = self.getControlCell(key, xpath);

    if (control) {
      control.changeCellVisibility(argument);
      for (var j = 0, length = self.columns.length; j < length; j++) {
        if (self.columns[j].properties.xpath == xpath) {
          break;
        }
      }

      self.cellOverrides[key] = self.cellOverrides[key] || {};
      self.getCellOverride(key, j).visible = argument;

      // Bind/Ubind the render change, so it can works with actions and validations as needed
      if (control.properties.editable && argument) {
        self.bindRenderChange(self.getColumn(xpath), key, true);
      } else {
        self.bindRenderChange(self.getColumn(xpath), key, false);
      }
    }
  },

