  isValidGrid: function isValidGrid(invalidElements) {
    var self = this;
    var errorsMessage = [];
    var form = self.getFormContainer();

    if (typeof form.validationController !== "undefined")
    form.validationController.clearValidationMessages();

    self.isValid(errorsMessage);

    if (errorsMessage.length > 0) {
      if (typeof form.validationController !== "undefined") {
        for (var i = 0, length = errorsMessage.length; i < length; i++) {
          // Show validation message
          form.validationController.showValidationMessage(errorsMessage[i].message, errorsMessage[i].xpath);
        }
      }

      return false;
    } else {
      return true;
    }
  },

