/*
 *   Name: Bizagi Smartphone Render MultiSelect Extension
 *   Author: UI Forms Team
 *   Comments:
 *   -   This script will redefine the MultiSelect render class to adjust to smartphone devices
 */

// Extends itself
bizagi.rendering.multiSelect.extend("bizagi.rendering.multiSelect", bizagi.rendering.render.base.prototype);
// Extends itself
bizagi.rendering.multiSelect.extend("bizagi.rendering.multiSelect", {
  MORE_OPTION_WIDTH: 40,
  SEARCH_MIN_LENGTH: 1,
  temporalElements: {} },
{

  /**
   * Template method to implement in each device to customize each render after processed
   */
  renderSingle: function renderSingle() {
    // Call base
    this._super();
  },

  postRenderSingle: function postRenderSingle() {
    if (this.properties.editable) {
      this.configureHandlers();
    } else {
      this.configureHandlersReadOnly();
    }
  },

  configureHandlers: function configureHandlers() {
    var $control = this.getControl();
    $(".bz-multiselect__arrow", $control).
    on("click", this.openModalView.bind(this));

    $('ul.bz-multiselect__tags', $control).
    on("click", "li div.bz-btn-icn", this.onUnselectElement.bind(this)).
    on("click", "li.bz-multiselect__plus", this.onShowSelectedElements.bind(this));
  },

  /**
      * Template method to implement in each device to customize the render's behaviour to add handlers when the control is readonly
      */
  configureHandlersReadOnly: function configureHandlersReadOnly() {
    var $control = this.getControl();

    $('ul.bz-multiselect__tags', $control).
    on("click", "li.bz-multiselect__plus", this.onShowSelectedElements.bind(this));
  },

  onUnselectElement: function onUnselectElement(ev) {
    var $element = $(ev.target);
    var idToRemove = $element.data("id");

    // Remove element
    this.removeElement({ id: idToRemove });
    this.addSelectedElements();
  },

  onShowSelectedElements: function onShowSelectedElements(ev) {
    var self = this;
    var $element = $(ev.currentTarget);
    var index = $element.data('index');
    var values = self.getDataValue();
    var valuesToShow = (values || []).slice(index);
    var template = this.renderFactory.getTemplate("list-multiselect-tags");
    var $listView = this.showFloatListView(template, {
      datasource: valuesToShow,
      editable: self.properties.editable });


    var $tagElements = $('ul.bz-multiselect__tags', $listView);

    $tagElements.on("click, mousedown", "li div.bz-btn-icn", function (ev) {
      self.onUnselectElement(ev);
      $listView.remove();
    });

    $listView.on("closeView", function () {
      $tagElements.off("click", "li div.bz-btn-icn", self.onUnselectElement.bind(self));
      $listView.remove();
    });
  },

  openModalView: function openModalView() {
    //Creating modal view
    var self = this;
    //Reset the temporal array
    self.Class.temporalElements = {};
    var template = self.renderFactory.getTemplate('multiselectModalView');
    var values = self.getDataValue();
    var options = {
      selected: values,
      isChecked: function isChecked(id) {
        return self.markAsChecked.call(this, id);
      } };


    this.getData().done(function (result) {
      var data = {
        'displayName': self.properties.displayName || "",
        "orientation": self.properties.orientation,
        "datasource": result,
        "options": options };

      try {
        var modalViewTemplate = kendo.template(template, { useWithBlock: false });
        for (var i = 0, length = values.length; i < length; i++) {
          self.Class.temporalElements[values[i].id] = values[i];
        }
        var modalView = $($.trim(modalViewTemplate(data)));
        self.modalListHandlers(modalView);

        modalView.kendoMobileModalView({
          close: function close() {
            self.triggerGlobalHandler("modalViewDidDisappear", {});
            this.destroy();
            this.element.remove();
          },
          useNativeScrolling: true,
          modal: false });


        self.configureModalViewHandlers(modalView);

        self.triggerGlobalHandler("modalViewDidAppear", {});
        modalView.kendoMobileModalView('open');
      } catch (e) {
        bizagi.log(e.message);
      }
    });
  },

  /**
      * Configure the modalView Handlers for the new combo control.
      * */
  configureModalViewHandlers: function configureModalViewHandlers(inputContainer) {
    var self = this;
    var $listContainer = inputContainer.find(".bz-multiselect__list li");
    var closeModalViewPromise = new $.Deferred();

    //Hide the clear text icon
    inputContainer.find(".bz-wp-modalview-header-cancel-search").hide(500);

    inputContainer.delegate("#ui-bizagi-cancel-button", "click", function () {
      closeModalViewPromise.reject();
      inputContainer.data("kendoMobileModalView").close();
    });

    //Showing found results
    inputContainer.find(".bz-wp-modalview-header-input-search").bind('keypress keyup', function () {
      if (this.value !== "") {
        inputContainer.find(".bz-wp-modalview-header-cancel-search").show(500);
      } else {
        inputContainer.find(".bz-wp-modalview-header-cancel-search").hide(500);
        $("#ui-bizagi-wp-filter-results-multiselect-wrapper ul li", inputContainer).show();
      }

      // Getting and showing the data
      self.filterList(this.value, $listContainer);
    }).focus(function () {
      inputContainer.find(".filter-searchlist-modal-view-tags").addClass("searching");
    }).blur(function () {
      inputContainer.find(".filter-searchlist-modal-view-tags").removeClass("searching");
    });

    //Cleaning search
    inputContainer.find(".bz-wp-modalview-header-cancel-search").bind('click', function () {
      $("#ui-bizagi-wp-filter-results-multiselect-wrapper ul li", inputContainer).show();
      inputContainer.find(".bz-wp-modalview-header-input-search").val("");
      $(this).hide(500);
    });

    //when click on apply button
    inputContainer.delegate(".ui-bizagi-apply-button", "click", function () {
      closeModalViewPromise.resolve();
      inputContainer.data("kendoMobileModalView").close();
    });

    inputContainer.find(".bz-wp-modalview-close").bind("click", function () {
      closeModalViewPromise.reject();
      inputContainer.data("kendoMobileModalView").close();
    });

    //Trigger delete event for tags
    inputContainer.find(".filter-searchlist-modal-view-tags").on("click", '.bz-rn-remove-tag-on-search-list', function () {
      self.removeItem($(this), true);

      var term = inputContainer.find(".bz-wp-modalview-header-input-search").val();

      // Getting and showing the data
      self.filterList(term, $listContainer);
    });

    $.when(closeModalViewPromise).done(function () {
      self.addNewValuesToInput();
      self.Class.temporalElements = {};
    }).fail(function () {
      self.Class.temporalElements = {};
    });
  },

  /**
      * Adding new values to input
      * */
  addNewValuesToInput: function addNewValuesToInput() {
    var self = this;

    //Create a temporal array
    var tempArray = [];

    $.each(self.Class.temporalElements, function (i, ele) {
      tempArray.push(ele);
    });

    self.setValue(tempArray);
    self.addSelectedElements();
  },

  /**
      * Method to render non editable values
      */
  postRenderReadOnly: function postRenderReadOnly() {
    // Call base
    this._super();
  },

  /**
      * addSelectedElements
      */
  addSelectedElements: function addSelectedElements() {
    var self = this;
    var $control = self.getControl();
    var values = self.getDataValue();
    var totalValues = values.length;
    var $tagsContainer = $('ul.bz-multiselect__tags', $control);
    var editable = this.properties.editable;
    $tagsContainer.empty();

    if (totalValues > 0) {

      var template = this.renderFactory.getTemplate("list-multiselect-tag");

      for (var i = 0; i < totalValues; i++) {
        var item = values[i];
        var data = { id: item.id, value: item.value, editable: editable };
        var elementTemplate = kendo.template(template, { useWithBlock: false });
        var $ChildToAdd = $($.trim(elementTemplate(data)));

        $tagsContainer.append($ChildToAdd);

        // Process the control in the layout
        this.processControlLayout(this.properties.valueFormat || {});

        if (!this.canAddMoreItems($tagsContainer)) {
          // Remove last item added
          $tagsContainer.
          children().
          last().
          remove();

          // Add the view mode item
          var data = {
            'value': totalValues - i,
            'index': i };


          var viewMoreTemplate = kendo.template(this.renderFactory.getTemplate("list-multiselect-view-more"), { useWithBlock: false });
          var $viewMore = $($.trim(viewMoreTemplate(data)));
          $tagsContainer.append($viewMore);
          break;
        }
      }
    }

  },

  modalListHandlers: function modalListHandlers($floatListContext) {
    var self = this;
    var $listContainer = $floatListContext.find(".bz-multiselect__list li");
    var checkbox = $listContainer.find("input[type=checkbox]");

    $floatListContext.find(".bz-multiselect__search_input").bind("change keypress keyup mousedown", function (evt) {
      evt.stopPropagation();

      self.filterList(this.value, $listContainer);
    });

    checkbox.change(function () {
      var $context = $(this);
      var isChecked = $context.is(":checked");
      var newValue = { id: $context.data("id"), value: $context.data("value") };

      if (isChecked) {
        self.addTemporalElement(newValue, false);
      } else {
        self.addTemporalElement(newValue, true);
      }
    });

    $floatListContext.on("closeView", function () {
      self.closeFloatView();

      $(document).unbind("mousedown.multiselect");
    });
  },

  addTemporalElement: function addTemporalElement(element, elementToRemove) {
    var self = this;
    if (elementToRemove) {
      delete self.Class.temporalElements[element.id];
    } else {
      self.Class.temporalElements[element.id] = element;
    }
  },

  showFloatListView: function showFloatListView(template, data) {
    var self = this;
    var $control = self.getControl();
    var multiSelect = $(".bz-multiselect", $control);

    var $containerControl = $control.closest(".ui-bizagi-render-control");
    var modalViewTemplate = kendo.template(template, { useWithBlock: false });
    var $floatList = $($.trim(modalViewTemplate(data))); //$.tmpl(template, data, options);
    var widthControl = multiSelect.width();

    $containerControl.append($floatList);

    $floatList.width(widthControl);

    $floatList.position({
      my: "left top",
      at: "left bottom",
      of: multiSelect,
      collision: "fit" }).
    hide();

    // Display view
    $floatList.show();

    // Stop bubbling outside the dropdown
    $floatList.on("mousedown", function (ev) {
      ev.preventDefault();
      return false;
    });

    // Add global handler to close the float view
    $(document).one("mousedown.multiselect", function (evt) {
      $floatList.trigger('closeView');
    });

    return $floatList;
  } });